import * as React from "react";
import PropTypes from "prop-types";
// import Button from "@mui/material/Button";
// import AccountCircleIcon from "@mui/icons-material/AccountCircle";
// import EditIcon from "@mui/icons-material/Edit";
// import PostAddIcon from "@mui/icons-material/PostAdd";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
// import CssBaseline from "@mui/material/CssBaseline";
// import EventAvailableIcon from "@mui/icons-material/EventAvailable";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
// import InboxIcon from "@mui/icons-material/MoveToInbox";
// import AssignmentIndIcon from "@mui/icons-material/AssignmentInd";
import List from "@mui/material/List";
// import LocalPostOfficeIcon from "@mui/icons-material/LocalPostOffice";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
// import MailIcon from "@mui/icons-material/Mail";
import MenuIcon from "@mui/icons-material/Menu";
// import WorkIcon from "@mui/icons-material/Work";
import HomeIcon from "@mui/icons-material/Home";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import CircleIcon from "@mui/icons-material/Circle";
import LocalAtmIcon from "@mui/icons-material/LocalAtm";
import WorkOutlineIcon from "@mui/icons-material/WorkOutline";
import AccountBoxIcon from "@mui/icons-material/AccountBox";
import ExitToAppIcon from "@mui/icons-material/ExitToApp";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
// import CardMedia from "@mui/material/CardMedia";
// import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";
// import SettingsApplicationsIcon from "@mui/icons-material/SettingsApplications";

import Toolbar from "@mui/material/Toolbar";
// import Typography from "@mui/material/Typography";

const drawerWidth = 300;

function ResponsiveDrawer(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <div>
      <h2 style={{ marginLeft: "18px", marginTop: "50px" }}>Turing</h2>
      {/* <Button> Avialable for</Button> */}
      {/* <div className='btton'>
        <WorkIcon />
        <div>
          <h4>Avialable for Jobs</h4>
          <div className="first">
          <h6 style={{color:'orange'}}>Expires in 12 Days</h6>
          <h6 style={{color:'blue'}}>Update Now</h6>
          </div>
        </div>
      </div> */}
      {/* <Toolbar /> */}
      {/* <Toolbar /> */}
      {/* <Divider /> */}
      <List>
        {["Home"].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>
              {/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}
              <HomeIcon style={{ color: "black" }} />
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
      <Divider />
      <List>
        {["Profile", "Turing Test", "Coding Challenge", "Get Matched"].map(
          (text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>
                {index % 4 === 0 ? (
                  <CheckCircleIcon style={{ color: "#33d65f" }} />
                ) : index % 4 === 1 ? (
                  <CircleIcon style={{ color: "#33d65f" }} />
                ) : (
                  <CircleIcon />
                )}
                {/* // index%4===0?<CheckCircleIcon />:index%4===1?<CircleIcon />: */}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          )
        )}
      </List>
      <Divider />

      <List>
        {["Refer & win $250", "Jobs", "Settings"].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>
              {index % 3 === 0 ? (
                <LocalAtmIcon style={{ color: "#33d65f" }} />
              ) : index % 3 === 1 ? (
                <WorkOutlineIcon />
              ) : (
                <AccountBoxIcon />
              )}
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
      <Toolbar />
      <List>
        {["Log Out"].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>
              <ExitToAppIcon />
              {/* {index % 3 === 0 ? <LocalAtmIcon /> : index%3===1?<WorkOutlineIcon />:<AccountBoxIcon />} */}
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
    </div>
  );

  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: "flex" }}>
      {/* <CssBaseline /> */}
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` }
          // color:'#787675'
          // color:'inherit'
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
          {/* <Typography variant="h6" noWrap component="div">
            Responsive drawer
          </Typography> */}
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
      >
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true // Better open performance on mobile.
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth
            }
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: "none", sm: "block" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth
            }
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{
          // flexGrow: 1,
          // p: 3,
          mt: 10,
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          // width: '1000px',
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
          alignContent: "center"
        }}
      >
        <Card
          sx={{
            // maxWidth: 700,
            width: "700px",

            margin: 1,
            display: "flex",
            // justifyContent: "center",
            pb: 3
            // alignItems: "center"
          }}
        >
          <CardActionArea>
            <CardContent>
              {/* <Typography gutterBottom variant="h6" component="div">
                Turing
              </Typography> */}
              {/* <Toolbar /> */}
              {/* <div style={{display:'flex' }}> */}

              {/* <Box
                // variant="h6"
                // component="div"
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "space-between",
                  paddingLeft: "10px",
                  paddingRight: "10px"
                }}
              >
                <Box
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "flex-start"
                  }}
                >
                  <PostAddIcon
                    style={{ height: "30px", width: "30px", marginRight: 10 }}
                  />
                  <Typography variant="h6" gutterBottom>
                    Upload Your Resume
                  </Typography>
                </Box>

                <EditIcon style={{ color: "#abb0ac" }} />
              </Box>
              </div> */}

              {/* <Typography variant="body2" color="text.primary"> */}
            </CardContent>
          </CardActionArea>
        </Card>
        <Card
          sx={{
            width: "700px",
            display: "flex",
            margin: 1,

            // justifyContent: "center",
            pb: 3
            // alignItems: "center"
          }}
        >
          <CardActionArea>
            <CardContent></CardContent>
          </CardActionArea>
        </Card>

        <Card
          sx={{
            // maxWidth: 700,
            width: "700px",
            // boxShadow:''
            display: "flex",
            margin: 1,
            // justifyContent: "center",
            pb: 3
            // alignItems: "center"
          }}
        >
          <CardActionArea>
            <CardContent></CardContent>
          </CardActionArea>
        </Card>
      </Box>
    </Box>
  );
}

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func
};

export default ResponsiveDrawer;
